import React, { Component, useState, useEffect, useRef } from "react";
import * as THREE from 'three';
import { OrbitControls } from '../three.js/examples/jsm/controls/OrbitControls.js';
import {TweenMax} from '../TweenMax.js';
class Animation extends Component {
    componentDidMount() {
        this.world = new THREE.Object3D();
        this.scrollStatus = 1 // 0: stole, 1: up, 2: down
        this.lastScrollTop =0;
        //scene
        this.scene = new THREE.Scene();

        //renderer
        this.renderer = new THREE.WebGLRenderer();
        this.renderer.setSize(window.innerWidth, window.innerHeight);
        this.renderer.shadowMap.enabled = true;
		this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
        this.mount.appendChild(this.renderer.domElement);
        
        //camera
        this.camera = new THREE.PerspectiveCamera(75, window.innerWidth/window.innerHeight, 0.1, 1000);
        this.camera.position.z = 5;
        
        // lights
        
        this.scene.background = new THREE.Color( 0xffffff );
        
        const ambient = new THREE.HemisphereLight( 0xffffff, 0xbfd4d2, 0.9 );
        this.scene.add( ambient );
        
        const directionalLight = new THREE.DirectionalLight( 0xffffff, 0.1 );
        directionalLight.position.set( 1, 3, 0 ).multiplyScalar( 3 );
        directionalLight.castShadow = true;
        directionalLight.shadow.mapSize.setScalar( 2048 );
        directionalLight.shadow.bias = - 1e-4;
        directionalLight.shadow.normalBias = 1e-4;
        this.scene.add( directionalLight );
        
        // create box
        //let geometry = new THREE.BoxGeometry(1,1,1);
        var geometry = new THREE.IcosahedronGeometry(0.5, 3);
        var circle_start = 10;
        for (var i =0; i<100; i++) {
            let material = new THREE.MeshStandardMaterial({
                color: 0xc1e5e3,
            });
            this.cube = new THREE.Mesh(geometry, material);
            this.cube.castShadow = true; //default is false
            this.cube.receiveShadow = true; //default

            this.cube.position.x = -Math.random()* circle_start + Math.random()* circle_start;
            this.cube.position.z = 0;//-Math.random()* circle_start + Math.random()* circle_start;
            this.cube.position.y = -Math.random()* circle_start + Math.random()* circle_start;
            var circle_scale = Math.random()* 1;
            this.cube.scale.set(circle_scale, circle_scale, circle_scale);
            var object_pos = this.world.children[ 0 ];
            var object_pos_range = 0;
            setInterval(function(){  
                object_pos.position.x = -Math.random() * object_pos_range + Math.random() * object_pos_range;
                object_pos.position.y = -Math.random() * object_pos_range + Math.random() * object_pos_range;
                object_pos.position.z = -Math.random() * object_pos_range + Math.random() * object_pos_range;
            }, 1000);
            this.world.add( this.cube );
        }
        
        // add to scene
        this.scene.add(this.world);

        // create plane
        
        const planegeometry = new THREE.PlaneGeometry( 10, 10 );
        
        const planematerial = new THREE.MeshStandardMaterial( {color: 0xffffff, transparent: true, opacity: 0.5,side: THREE.DoubleSide} );//new THREE.ShadowMaterial( {color: 0xffff00, transparent: true, opacity: 0.075,side: THREE.DoubleSide} );
        const plane = new THREE.Mesh( planegeometry, planematerial );
        plane.quaternion.setFromAxisAngle(new THREE.Vector3(1,0,0), Math.PI/2);
        plane.position.set(0,-1,0);
        plane.receiveShadow = true;
        this.scene.add( plane );
        
        var primitiveElement = function() {
            this.mesh = new THREE.Object3D();
            var geo = new THREE.IcosahedronGeometry();
            var mat = new THREE.MeshBasicMaterial({color:0x500000, flatShading:false});
            var mesh = new THREE.Mesh(geo, mat);
        //---
        //this.mesh.add(mesh);
        //---
        }
        this._primitive = new primitiveElement();
        function createPrimitive() {
            this._primitive.mesh.scale.set(1,1,1);
            this.scene.add(this._primitive.mesh);
        }
        this.animation();
        this.renderer.render(this.scene, this.camera);

        //event handelers
        window.addEventListener("resize", this.handelWindowResize)
        const navi_bar = document.querySelector('.Navi-bar');
        
        window.addEventListener("scroll", ()=> {
            const navi_bar_rect = navi_bar.getBoundingClientRect();
            if(navi_bar_rect.bottom > 0) {
                this.scene.background = new THREE.Color( 0xffffff );
                plane.material.color.setRGB(1,1,1);
            }
            else {
                this.scene.background = new THREE.Color( 0x000000 );
                plane.material.color.setRGB(0,0,0);
            }
        })
        
        window.addEventListener("scroll", () => {
            var st = window.pageYOffset || document.documentElement.scrollTop;
            if (st > this.lastScrollTop) {
                // scroll down
                this.scrollStatus = 1;
            } else {
                // scroll up
                this.scrollStatus = 2;
            }
            this.lastScrollTop = st <= 0? 0 : st;
        }, false);
    }

    animation = ()=> {
        requestAnimationFrame(this.animation);
        
        var time = Date.now() * 0.003;
        this._primitive.mesh.rotation.y += 0.003;
        this.world.rotation.y = Math.sin(time) * Math.PI / 180;
        this.world.rotation.z = Math.cos(time) * Math.PI / 180;
        var object_place = this.world.children[ 0 ];
        object_place.visible = false;
        //---
        for ( let i = 0, l = this.world.children.length; i < l; i ++ ) {
            var object = this.world.children[ i ];
            var object_left = this.world.children[ i-1 ];
           
            if (i>0) {
                
                //---
                //object.position.x += Math.sin(time + object_left.position.x) - object.position.x * 0.8;
                //object.position.y += Math.cos(time + object_left.position.y) - object.position.y * 0.8;
                
                if (this.scrollStatus == 1) {
                    // up
                    TweenMax.to(object.position, 3, {
                    x:Math.cos(object_left.position.x * Math.PI) * 1,
                    y:Math.sin(object_left.position.y * Math.PI) * 1,
                    //z:Math.cos(object_left.position.z * Math.PI) * 1,
                    //elay:0.001*i,
                    delay:1,
                    //ease:Sine.easeOut
                    //ease:Expo.easeOut
                    });
                }
                else if (this.scrollStatus == 2){
                    // down
                    TweenMax.to(object.position, 3, {
                    x:Math.cos(object_left.position.x * Math.PI) * 3,
                    y:Math.sin(object_left.position.y * Math.PI) * 1,
                    z:Math.cos(object_left.position.z * Math.PI) * 3,
                    //elay:0.001*i,
                    delay:1,
                    //ease:Sine.easeOut
                    //ease:Expo.easeOut
                    });
                    
                }
                
                
                              
             var tempColor = new THREE.Color().setHSL(215, 219, 217);
            TweenMax.to(object.material.color, 5, { r: tempColor.r, g: tempColor.g, b: tempColor.b });
            
            //---
            }
        }
        var object_speed = 0.6;
        var object_guide = this.world.children[ 1 ];
        object_guide.position.x += Math.sin(object_place.position.x) - object_guide.position.x * object_speed;
        object_guide.position.y += Math.cos(object_place.position.y) - object_guide.position.y * object_speed;
        object_guide.position.z += object_place.position.z - object_guide.position.z * object_speed;
        //---
        this.camera.lookAt(this.scene.position);
        this.renderer.render(this.scene, this.camera);
    }

    handelWindowResize = () => {
        this.camera.aspect = window.innerWidth/ window.innerHeight;
        this.camera.updateProjectionMatrix();

        this.renderer.setSize(window.innerWidth, window.innerHeight);
        this.renderer.render(this.scene, this.camera);
    }
    
    render() {
        return (
            <div
            ref={mount => {
                this.mount = mount;
            }}
        />
        )
    }
}
export default Animation